<template>
  <div id="home">
    <h1 class="wine_home_title">20221230版本迭代酒柜APP</h1>
    <div class="wine_home_box" @click="$router.push({ path:'/exact/wineCardMatrix',query:{language: 'en',from: 'html',generic:'false',found: 'false',inventoryFlag:'true'}})">酒品库存列表exact_v12开发版本</div>
    <div class="wine_home_box" @click="$router.push({ path:'/generic/wineCardLibrary',query:{language: 'en',from: 'html',generic:'true',found:'false',inventoryFlag:'true'}})">酒品库存列表generic_v12开发版本</div>
    <div class="wine_home_box" @click="$router.push({ path:'/showWine2',query:{language: 'en',from: 'html',generic:'false',found:'false',inventoryFlag:'false'}})">添加酒-在vivino存在_v12开发版本</div>
    <div class="wine_home_box" @click="$router.push({ path:'/photoData2',query:{language: 'en',from: 'html',generic:'true',found:'false',inventoryFlag:'false'}})">添加酒-在vivino未存在_v12开发版本</div>
    <div class="wine_home_box" @click="$router.push({ path:'/deviceSettings',query:{language: 'en',from: 'html',deviceId: '01',wifiId:'456',onlineStatus:'1',oneZoneFlag:'0'}})">SETTINGS_v12版本</div>
    <div class="wine_home_box" @click="$router.push({ path:'/newLights/newLights',query:{language: 'en',from: 'html',deviceId: '01',oneZoneFlag:'0'}})">酒柜灯光调节lights v12开发版本</div>
    <div class="wine_home_box" @click="$router.push({ path:'/temperature',query:{language: 'en',from: 'html',deviceId: '01',oneZoneFlag:'0',onlineStatus:'1'}})">温度湿度调节</div>
    <div class="wine_home_box" @click="$router.push({ path:'/echarts',query:{language: 'en',from: 'html',deviceId: '01',oneZoneFlag:'0'}})">图表</div>
    <div class="wine_home_box" @click="$router.push({ path:'/addwine2',query:{language: 'en',from: 'html',generic:'false',}})">手动添加酒</div>
  </div>
</template>
<script type="text/ecmascript-6">
  export default {
    name: "Home"
  }
</script>
<style>
  #home .wine_home_title {
    width: 100%;
    padding: 20px 0;
    text-align: center;
  }
  #home .wine_home_box {
    width: 92%;
    padding: 20px;
    margin: 0 auto 15px;
    font-size: 16px;
    text-align: center;
    background: #fff;
    border-radius: 10px;
    box-shadow: 2px 2px 10px #ddd;
    cursor: pointer;
  }
  /* zh_cn */
</style>



